@font-face {
  font-family: "bouldregular";
  src: url("../fonts/Bould font/bould-regular-webfont.ttf") format("truetype");
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "bouldregular";
}

/* page 1 */

.main {
  width: 100vw;
  height: 100vh;
  display: flex;
}

.page-1 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 41px;
  flex: 1;
  background: #a9e0dd;
  border-radius: 16px;
  gap: 8rem;
}

.search-box {
  display: flex;
  background: #fcfcff;
  border: 3px solid #29847f;
  border-radius: 60px;
  width: 40%;
}

.search-bar {
  border: none;
  font-weight: 600;
  font-size: 30px;
  border-radius: 60px;
  padding-left: 20px;
  outline: none;
  width: 80%;
}

.search-bar::placeholder {
  color: #000000;
}

.search-icon {
  width: 75px;
  height: 75px;
}

.button-box {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.start-button {
  background: #29847f;
  border-radius: 16px;
  border: none;
  width: 164px;
  height: 60px;
  color: white;
  font-size: 30px;
  cursor: pointer;
}

.button-box span {
  color: #29847f;
  display: flex;
  justify-content: center;
}

/* page 2 */

.page-2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 41px;
  flex: 1;
  background: #a9e0dd;
  border-radius: 16px;
}

.form-outer-box {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-box {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.form-box form {
  display: flex;
  flex-direction: column;
  padding: 8rem 6rem 8rem 4rem;
  gap: 6rem;
}

.input-wrapper {
  display: flex;
  align-items: center;
}

.input-box {
  display: flex;
  flex-direction: column;
  font-weight: 500;
  font-size: 20px;
  width: 100%;
}

.input-box input {
  border: none;
  border-bottom: 1px solid #29847f;
  outline: none;
  background: transparent;
  font-weight: 300;
  font-size: 20px;
}

.input-box input::placeholder {
  font-weight: 300;
  font-size: 12px;
  color: #29847f;
}
.input-wrapper span {
  font-weight: 500;
  font-size: 64px;
  padding-right: 0.5rem;
}

.svg-box {
  width: 50%;
  height: 100%;
  background-image: url("./images/marten-newhall-uAFjFsMS3YY-unsplash\ 1.svg");

  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-bottom-right-radius: 16px;
  border-top-right-radius: 16px;
}

/* Button */

.btn {
  background: rgba(221, 255, 253, 0.66);
  border: 1px solid #29847f;
  border-radius: 4px;
  min-width: 10rem;
  height: 2.4rem;
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
  padding-right: 0.5rem;
  cursor: pointer;
}

.btn-selected {
  background: white;
}

/*
. btn:focus {
  background-color: #ffffff;
} 

.btn:active {
  background-color: white;
} */

.btn-option {
  background: #29847f;
  min-width: 1.5rem;
  min-height: 1.5rem;
  margin: 0.6rem;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}

.btn-text {
  color: #29847f;
}

/* questions-page */

.questions-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 41px;
  flex: 1;
  background: #a9e0dd;
  border-radius: 16px;
  padding-left: 4rem;
}

.question {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 20px;
}

.question span {
  font-weight: 500;
  font-size: 64px;
  padding-right: 0.5rem;
}

.btn-box {
  padding-left: 2.6rem;
}
.btn-box p {
  font-weight: 600;
  font-size: 14px;
  text-decoration-line: underline;
  color: #29847f;
}

/* navigation button */

.nav-btn {
  color: #ffffff;
  background-color: #29847f;
  border: none;
  height: 1.5rem;
  width: 4rem;
  border-radius: 0.5rem;
  cursor: pointer;
 
}

.navigation-box {
  display: flex;
  gap: 2rem;
  padding-top: 1rem;
}
.link {

  text-decoration: none;
}

/* rating page */

.btn-rating-box {
  display: flex;
  gap: 0.5rem;
  padding: 1rem;
}

.btn-rating {
  height: 3rem;
  width: 3rem;
  color: #29847f;
  background: #a9e0dd;
  border: 1px solid #29847f;
  cursor: pointer;
}

.btn-rating-selected {
  background: white;
}
